import axios from 'axios';

const jsonRequest = axios.create({
    baseURL: '/api', // APIURL
    timeout: 5000, // 
    headers: {
        'Content-Type': 'application/json',
    },
});

const fileRequest = axios.create({
    baseURL: '/api', // APIURL
    timeout: 5000, // 
    headers: {
        'Content-Type': 'multipart/form-data',
    },
});

export {jsonRequest, fileRequest};