const fillMissingDates = (datalist: any[], startDate: Date, endDate: Date): any[] => {
    // key0map
    let dataMap = new Map();
    for (let d = startDate; d <= endDate; d.setDate(d.getDate() + 1)) {
        let dateStr = formatDate(d);  //  'yyyy-mm-dd'
        dataMap.set(dateStr, 0);
    }

    // datalist
    datalist.forEach(data => {
        let dateStr = data.txnDate.slice(0, 10);  //  'yyyy-mm-dd'
        dataMap.set(dateStr, data.txnAmount);
    });

    // map
    let result: any[] = [];
    dataMap.forEach((value, key) => {
        result.push({
            txnAmount: value,
            txnDate: key
        });
    });

    return result;
}

//  'yyyy-mm-dd'
const formatDate = (date: Date): string => {
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();

    return `${year}-${month.toString().padStart(2, '0')}-${day.toString().padStart(2, '0')}`;
}

export {fillMissingDates};